/* Copy all styles from previous Home.css but update class names from home- to vehicle-imports- */
.vehicle-imports {
  /* Same styles as .home */
  width: 100%;
}

.featured-section {
  background-color: #f9f9f9;
  padding: 60px 0;
}

.vehicle-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 30px;
  margin-top: 40px;
}

.vehicle-card {
  background-color: white;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.vehicle-card:hover {
  transform: translateY(-10px);
}

.vehicle-image-container {
  width: 100%;
  padding-top: 66.67%;
  position: relative;
  overflow: hidden;
}

.vehicle-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: transform 0.5s ease;
  background-color: #f5f5f5;
}

.vehicle-card:hover .vehicle-image {
  transform: scale(1.05);
}

@media screen and (max-width: 768px) {
  .vehicle-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
  }
}
