.vehicle-request-hero {
  background-color: var(--primary-color);
  color: var(--text-light);
  padding: 100px 0 50px;
  text-align: center;
}

.vehicle-request-hero h1 {
  font-size: 3rem;
  margin-bottom: 10px;
}

.vehicle-request-hero p {
  font-size: 1.2rem;
  max-width: 600px;
  margin: 0 auto;
}

.form-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 30px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.form-container h2 {
  color: var(--primary-color);
  margin-bottom: 30px;
  text-align: center;
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: var(--primary-color);
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 12px 15px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.form-group input[type="text"],
.form-group select {
  width: 100%;
  padding: 12px 15px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
  transition: border-color 0.3s ease;
  background-color: white;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  border-color: var(--secondary-color);
  outline: none;
}

.form-group input[type="text"]:focus,
.form-group select:focus {
  border-color: var(--secondary-color);
  outline: none;
}

.submit-btn {
  width: 100%;
  margin-top: 20px;
}

.success-message {
  text-align: center;
  padding: 30px;
  background-color: #e8f5e9;
  border-radius: 8px;
  margin-top: 20px;
}

.success-message p {
  color: #2e7d32;
  font-size: 1.1rem;
  margin: 0;
}

.button-group {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}

.submit-btn,
.reset-btn {
  flex: 1;
  margin-top: 0;
}

.reset-btn {
  background-color: #e0e0e0;
  color: var(--primary-color);
}

.reset-btn:hover {
  background-color: #d0d0d0;
}

@media screen and (max-width: 768px) {
  .form-grid {
    grid-template-columns: 1fr;
  }
  
  .vehicle-request-hero h1 {
    font-size: 2rem;
  }
  
  .form-container {
    padding: 20px;
  }
  
  .button-group {
    flex-direction: column;
  }
}
