.hero-section {
  height: 80vh;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  color: var(--text-light);
  padding: 0 20px;
  overflow: hidden;
}

.hero-section::before {
  content: '';
  position: absolute;
  top: 0;
}

.hero-content h1 {
  font-size: 3rem;
  margin-bottom: 20px;
  font-weight: 700;
  line-height: 1.2;
}

.highlight {
  color: var(--secondary-color);
}

.hero-content p {
  font-size: 1.2rem;
  margin-bottom: 40px;
}

.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.btn-outline {
  background-color: transparent;
  border: 2px solid var(--secondary-color);
  color: var(--text-light);
}

.btn-outline:hover {
  background-color: var(--secondary-color);
  color: var(--primary-color);
}

@media screen and (max-width: 768px) {
  .hero-section {
    height: 500px;
  }
  
  .hero-content h1 {
    font-size: 2rem;
  }
  
  .hero-content p {
    font-size: 1rem;
  }
  
  .hero-buttons {
    flex-direction: column;
    gap: 15px;
  }

  .hero-buttons .btn {
    width: auto;
    min-width: 140px;
    padding: 10px 25px;
  }
}
